            const hasAnyActive = updatedPackagings.some(p => p.deliveryStatus && p.deliveryStatus !== 'ƒê√£ h·ªßy' && p.status !== 'H·ªßy ƒë√≥ng g√≥i');
            
            let newOrderStatus = order.status;
